<template>
    <div class="menu">
        <a href="#index" @click="updateActiveKey('index')">
            <img alt="Vue logo" src="../assets/logo.png">
        </a>
        <ul>
            <li
                v-for="item in menu"
                v-bind:key="item.id"
                id="item.id"
                :class="activeKey === item.id ? 'active' : ''"
            >
                <a :href="'#'+item.id" @click="updateActiveKey(item.id)">{{item.name}}</a>
                <div></div>
            </li>
            <li>
                <a href="">EN</a>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    name: "MenuHoc",
    data(){
        return {
            menu: [{
                name: "西遇首页",
                enName: "Home",
                id: "index"
            },{
                name: "产品服务",
                enName: "Products",
                id: "products"
            },{
                name: "关于西遇",
                enName: "About Us",
                id: "about"
            },{
                name: "联系我们",
                enName: "Contact Us",
                id: "contact"
            }],
            activeKey: 'index'
        }
    },
    methods: {
        updateActiveKey(key) {
            this.activeKey = key
        }
    }
}
</script>

<style scoped>
.menu{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 150px;
    z-index: 999;
    color: #000;
    background: #ffffff;
    overflow: hidden;
}
ul>li{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    line-height: 50px;
    float: left;
    margin: 0 15px;
    list-style-type: none;
}
ul>li>a{
    color: #000;
    text-decoration: none;
}
.active div{
    width: 65px;
    height: 2px;
    background: #9E0CBB;
}
.active a, a:active, a:hover{
    color: #9E0CBB
}
@media only screen and (max-width: 600px){
    .menu{
        padding: 0 0 0 20px;
    }
    .menu img{
        width: 80px;
    }
    ul>li{
        margin: 0 3px;
        font-size: 14px;
    }
}
</style>