<script>
import MenuHoc from './components/MenuHoc.vue';
import TitleHoc from './components/TitleHoc.vue';

export default {
  name: 'App',
  components: {
    MenuHoc,
    TitleHoc
  },
  data() {
    return {
      ads: [{
        key: 0,
        name: 'Facebook',
        icon: require('./assets/facebook.png'),
        ads: require('./assets/facebookdesc.png')
      },{
        key: 1,
        name: 'Google',
        icon: require('./assets/google.png'),
        ads: require('./assets/googledesc.png')
      },{
        key: 2,
        name: 'TikTok',
        icon: require('./assets/tiktok.png'),
        ads: require('./assets/tiktokdesc.png')
      },{
        key: 3,
        name: 'Twitter',
        icon: require('./assets/twitter.png'),
        ads: require('./assets/twitterdesc.png')
      },{
        key: 4,
        name: 'Snapchat',
        icon: require('./assets/snapchat.png'),
        ads: require('./assets/snapchatdesc.png')
      }],
      currentAds: 0,
      timer: ''
    }
  },
  methods: {
    updateAds(key) {
      this.currentAds = key;
    }
  },
  mounted() {
    this.timer = setInterval(()=>{
      if(this.currentAds === 4) {
        this.currentAds = 0;
      }else{
        this.currentAds+=1;
      }
    },5000)
  },
  beforeUnmount(){
    clearInterval(this.timer)
  }
}
</script>

